.containerHeader {
  background-color: white;
}
.containerHeader .top {
  text-align: right;
  border-bottom: 2px solid #f9c700;
  height: 60px;
  line-height: 60px;
}
.containerHeader .top .topRight {
  padding-right: 20px;
}
.containerHeader .bottom {
  height: 44px;
  line-height: 44px;
  text-align: center;
}
.containerHeader .bottom .triangle {
  border: 15px solid transparent;
  border-top: 15px solid white;
  width: 0px;
  height: 0px;
  position: absolute;
  top: 44px;
  left: 90px;
}
.containerHeader .bottom .bottomRight {
  text-align: right;
  padding-right: 20px;
}
